import service from '@/utils/request';
export const GetMyAccountInfoAsync = () => service.request({
    url: '/api/user/me/',
    method: 'get',
});
export const AdminGetUsersByClassroomIdAsync = (classroomId) => service.request({
    url: `/api/admin/user/classroom/${classroomId}/`,
    method: 'get'
});
export const AdminGetAccountByUserIdAsync = (userId) => service.request({
    url: `/api/admin/user/${userId}/`,
    method: 'get'
});
